@font-face {
  font-family: 'montserrat';
  src: url("../fonts/montserrat-bold-webfont.woff2") format("woff2"), url("../fonts/montserrat-bold-webfont.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'montserrat';
  src: url("../fonts/montserrat-regular-webfont.woff2") format("woff2"), url("../fonts/montserrat-regular-webfont.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

* {
  box-sizing: border-box;
  margin: 0;
}

.body {
  font-family: 'montserrat', 'Arial', sans-serif;
  font-style: normal;
  font-weight: 400;
}

.container {
  max-width: 767px;
  min-width: 315px;
  margin: 0 auto;
  padding: 0 30px;
}

.container--wide {
  width: 100%;
  padding: 0;
}

.container--flex-column {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.wrapper {
  background-image: url(../img/background-img.svg), linear-gradient(228.57deg, #323264 11.93%, #323264 57.17%, #643E72 87.46%);
  background-repeat: no-repeat;
  background-position: top center;
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .container {
    min-width: 648px;
    max-width: 1023px;
    padding: 0 60px;
  }
  .container--wide {
    width: inherit;
  }
}

.header {
  display: grid;
  grid-template-rows: auto auto;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  margin: 0 15px;
}

.header__languages {
  list-style: none;
  display: flex;
  align-items: center;
  padding-left: 15px;
  padding-top: 10px;
  padding-bottom: 14px;
  background-color: rgba(245, 245, 250, 0.2);
  margin-left: -15px;
}

.header__languages-list-item {
  text-decoration: none;
  padding: 5px 7px;
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  text-align: center;
  letter-spacing: 1px;
  color: white;
  text-transform: uppercase;
  border-radius: 8px;
  transition: background-color 0.3s;
}

.header__languages-list-item:hover:not(.header__languages-list-item--activated) {
  background-color: rgba(255, 255, 255, 0.2);
}

.header__languages-list-item--activated {
  background-color: #F0145A;
}

.header__address {
  font-size: 16px;
  line-height: 20px;
  text-align: right;
  color: #B3B3BA;
  display: none;
  align-items: center;
}

.header__address::before {
  content: '';
  width: 6px;
  height: 6px;
  background-color: #E6E6F0;
  display: inline-flex;
  border-radius: 50%;
  margin-right: 6px;
}

.header__address::after {
  content: '';
  width: 6px;
  height: 6px;
  background-color: #E6E6F0;
  display: inline-flex;
  border-radius: 50%;
  margin-left: 6px;
}

.header__phone {
  background-color: rgba(245, 245, 250, 0.2);
  padding-right: 15px;
  padding-top: 10px;
  padding-bottom: 15px;
  margin-right: -15px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.header__phone::after {
  content: '';
  width: 6px;
  height: 6px;
  background-color: #E6E6F0;
  display: none;
  border-radius: 50%;
  margin-left: 6px;
}

.header__phone-item {
  text-decoration: none;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  text-align: right;
  color: white;
  transition: color 0.3s;
}

.header__phone-item:hover {
  color: #F0145A;
}

.header__facebook {
  display: none;
}

.header__facebook:hover svg path {
  fill: #F0145A;
}

.header__facebook path {
  transition: fill 0.3s;
}

.header__nav {
  display: none;
}

.header__nav a {
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  text-align: right;
  text-decoration: none;
  color: white;
  margin-right: 15px;
  border-bottom-width: 2px;
  border-bottom-style: solid;
  border-bottom-color: transparent;
  transition: color 0.3s;
  transition: border 0.3s;
}

.header__nav a:hover {
  border-bottom-color: white;
}

.header__nav a:last-of-type {
  margin-right: 0;
}

.header__page {
  display: none;
}

.header__logo {
  justify-self: flex-start;
  margin: 15px 0 10px;
  height: 32px;
}

.header__logo-img-responsive {
  object-fit: contain;
  height: 100%;
}

.header__burger {
  justify-self: flex-end;
  font-size: 0;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  border-radius: 8px;
}

.header__burger:active {
  background-color: #E6E6F0;
}

.header__burger:active svg path {
  stroke: #323264;
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .header {
    grid-template-rows: auto 1fr auto;
    grid-template-columns: 100px 221px 116px auto;
    grid-column-gap: 6px;
    grid-row-gap: 30px;
    margin: 0;
    padding-top: 50px;
  }
  .header__languages {
    padding-top: 0;
    padding-bottom: 0;
    background-color: transparent;
    margin-left: 0;
    padding-left: 0;
    width: 99px;
  }
  .header__phone {
    background-color: transparent;
    padding-right: 0;
    padding-top: 0;
    padding-bottom: 0;
    margin-right: 0;
    justify-content: center;
  }
  .header__phone::after {
    display: inline-flex;
  }
  .header__facebook {
    display: inline-flex;
    align-items: center;
    width: 36px;
  }
  .header__nav {
    display: inline-flex;
    width: 416px;
    grid-row-start: 3;
    grid-column-start: 1;
    grid-column-end: span 3;
  }
  .header__address {
    display: inline-flex;
    width: 221px;
  }
  .header__burger {
    display: none;
  }
  .header__logo {
    height: auto;
    grid-column-start: 1;
    grid-column-end: span 3;
    margin: 0;
  }
}

.solution {
  padding-top: 129px;
}

.solution__box {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.solution__title {
  font-weight: 700;
  font-size: 38px;
  line-height: 46px;
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 177px;
}

.solution__arrow {
  display: inline-flex;
  width: 42px;
  height: 42px;
  opacity: 0.5;
  border: 2px solid #FFFFFF;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin-bottom: 75px;
  transition: border 0.3s;
}

.solution__arrow path {
  transition: stroke 0.3s;
}

.solution__arrow:hover svg path {
  stroke: #F0145A;
}

.solution__arrow:hover {
  border-color: #F0145A;
  opacity: 1;
}

.solution__arrow svg {
  margin-top: 3px;
}

.solution__scroll {
  display: flex;
  overflow: auto;
  align-self: flex-start;
  -ms-overflow-style: none;
  scrollbar-width: none;
  margin-bottom: 30px;
  margin-right: -30px;
}

.solution__scroll::-webkit-scrollbar {
  display: none;
}

.solution__scroll-button {
  white-space: nowrap;
  font-weight: 700;
  font-size: 30px;
  line-height: 37px;
  color: #B3B3BA;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  margin-right: 30px;
}

.solution__scroll-button--activated {
  background: linear-gradient(90.13deg, #F0145A 0.84%, #EF8080 99.96%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.solution__scroll-button:last-of-type {
  margin-right: 0;
}

.solution__text {
  border-top-width: 4px;
  border-top-style: solid;
  border-top-color: rgba(255, 255, 255, 0.3);
  padding-top: 30px;
  display: none;
}

.solution__text--activated {
  display: block;
}

.solution__text h3 {
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  color: white;
}

.solution__text p {
  font-size: 19px;
  line-height: 30px;
  color: white;
  margin-bottom: 30px;
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .solution {
    padding-top: 269px;
  }
  .solution__title {
    margin-bottom: 417px;
  }
  .solution__arrow {
    margin-bottom: 105px;
  }
  .solution__scroll {
    margin-bottom: 60px;
  }
  .solution__text {
    padding-top: 60px;
  }
  .solution__text p {
    margin-bottom: 60px;
  }
}

.services__heading {
  margin-top: 10px;
  font-weight: 700;
  font-size: 38px;
  line-height: 46px;
  background: linear-gradient(90.13deg, #F0145A 0.84%, #EF8080 99.96%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.services__offers {
  display: flex;
  flex-direction: column;
  padding-bottom: 60px;
}

.services__offers-item {
  margin-top: 30px;
}

.services__offers-item-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 92px;
  height: 92px;
  background-color: white;
  border-radius: 24px;
  margin-bottom: 30px;
}

.services__offers-item-heading {
  font-weight: 700;
  font-size: 22px;
  line-height: 27px;
  color: #24A3FF;
  margin-bottom: 15px;
}

.services__offers-item-paragraph {
  font-size: 16px;
  line-height: 26px;
  color: white;
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .services__offers {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-column-gap: 60px;
  }
  .services__offers-item {
    margin-top: 60px;
  }
}

.team {
  position: relative;
  padding-top: 50px;
}

.team::after {
  content: '';
  display: block;
  background: #CACADB;
  border-radius: 0px 0px 12px 12px;
  height: 10px;
  position: absolute;
  top: 0;
  bottom: 50%;
  left: 20px;
  right: 20px;
}

.team::before {
  content: '';
  display: block;
  background: #E7E7F2;
  border-radius: 0px 0px 12px 12px;
  height: 10px;
  position: absolute;
  left: 40px;
  right: 40px;
  top: 10px;
  bottom: 0;
}

.team__card {
  display: none;
  width: 100%;
}

.team__card--activated {
  display: block;
}

.team__card-pic {
  height: 500px;
  width: 100%;
  background-image: url(../../img/team-face.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 12px 12px 0px 0px;
}

.team__card-text {
  padding-top: 30px;
  background-color: #EDEDF7;
  border-radius: 0px 0px 12px 12px;
  padding-bottom: 30px;
}

.team__card-box {
  margin: 0 30px;
}

.team__card-box-name {
  margin-top: 30px;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: #B3B3BA;
}

.team__card-box-job {
  font-size: 14px;
  line-height: 22px;
  color: #B3B3BA;
  margin-top: 6px;
}

.team__scroll {
  margin-top: 30px;
}

.team__scroll-button {
  background-color: #24A3FF;
  border: none;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  opacity: 0.2;
  margin-right: 15px;
  cursor: pointer;
}

.team__scroll-button--activated {
  opacity: 1;
}

.team__scroll-button:last-of-type {
  margin-right: 0;
}

.page-link {
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 1px;
  text-transform: uppercase;
  text-decoration: none;
  color: #24A3FF;
}

.partners {
  padding-top: 60px;
  text-align: center;
}

.partners__heading {
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #323264;
  margin-bottom: 30px;
}

.partners__box {
  display: flex;
  flex-direction: column;
}

.partners__box-item {
  height: 120px;
  width: auto;
  background-repeat: no-repeat;
  background-position: center center;
}

.partners__box-item--hidden {
  display: none;
}

.partners__box-item--hillel {
  background-image: url(../../img/hillel.png);
  background-color: #1C4280;
}

.partners__box-item--wannabiz {
  background-image: url(../../img/wannabiz.png);
  background-color: #143646;
}

.partners__box-item--tapgerine {
  background-image: url(../../img/tapgerine.png);
  background-color: #F48B30;
}

.partners__box-item--3d-look {
  background-image: url(../../img/3d-look.png);
  background-color: #9E4DC3;
}

.partners__box-item--orangear {
  background-image: url(../../img/orangear.png);
  background-color: #FD9339;
}

.partners__box-item--adtelligent {
  background-image: url(../../img/adtelligent.png);
  background-color: #E2E2E4;
}

.partners__box-item--clickky {
  background-image: url(../../img/clickky.png);
  background-color: #C71C36;
}

.partners__box-item--ecoleasing {
  background-image: url(../../img/infratek.png);
  background-color: #DCF3F9;
}

.partners__box-item--infratek {
  background-image: url(../../img/infratek.png);
  background-color: #EAEAEA;
}

.partners__box-item--citypark {
  background-image: url(../../img/city-park.png);
  background-color: #282240;
}

.partners__box-item--art-print {
  background-image: url(../../img/art-print.png);
  background-color: #1D4568;
}

.partners__box-item--ultimate-card {
  background-image: url(../../img/ultimate-card.png);
  background-color: #000000;
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .partners__box {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .partners__box-item {
    width: 33.333%;
  }
  .partners__box-item--hidden {
    display: block;
  }
}

.publications {
  padding-top: 30px;
  background-color: #F5F5FA;
}

.publications__box {
  margin-top: 30px;
  border-bottom: #E6E6F0 solid 4px;
}

.publications__box-item {
  margin-bottom: 30px;
}

.publications__box-item-pic-link {
  display: block;
  height: 200px;
}

.publications__box-item-pic-link img {
  border-radius: 8px 8px 0px 0px;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.publications__box-item-text {
  background-color: white;
  border-radius: 0px 0px 8px 8px;
}

.publications__box-item-text-content {
  padding: 30px;
}

.publications__box-item-text-content-link {
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: #006CCF;
  text-decoration: none;
}

.publications__box-item-text-content-date {
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 1px;
  text-transform: uppercase;
  padding: 15px 0;
  color: #B3B3BA;
  border-bottom: #E6E6F0 solid 4px;
}

.publications__box-item-text-content-info {
  font-size: 14px;
  line-height: 22px;
  margin-top: 15px;
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .publications__box {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr auto;
    grid-column-gap: 30px;
    grid-row-gap: 30px;
  }
  .publications__box-item {
    display: flex;
    flex-direction: column;
    margin-bottom: 0;
  }
  .publications__box-item-text {
    height: 100%;
  }
  .publications__box-item:last-of-type {
    grid-column-start: 1;
    grid-column-end: 3;
    margin-bottom: 30px;
  }
  .publications__box-item:last-of-type .publications__box-item-text {
    height: auto;
  }
}

.heading-gradient {
  background: linear-gradient(90.13deg, #F0145A 0.84%, #EF8080 99.96%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 700;
  font-size: 30px;
  line-height: 37px;
  margin-top: 10px;
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .heading-gradient {
    font-size: 38px;
    line-height: 46px;
  }
}

.footer {
  background-color: #F5F5FA;
  padding-top: 30px;
  padding-bottom: 30px;
}

.footer__box {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer__box-address {
  font-size: 16px;
  line-height: 20px;
  color: #323264;
}

.footer__box-link {
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 1px;
  text-transform: uppercase;
  text-decoration: none;
  color: #24A3FF;
  margin-top: 10px;
  margin-bottom: 21px;
  transition: color 0.3s;
}

.footer__box-link:hover {
  color: #F0145A;
}

.footer__box-facebook {
  text-decoration: none;
  font-size: 0;
}

.footer__box-facebook:hover svg path {
  fill: #F0145A;
}

.footer__box-facebook path {
  fill: #24A3FF;
  transition: fill 0.3s;
}

.footer__box-phone {
  margin-top: 10px;
  margin-bottom: 10px;
}

.footer__box-phone-link {
  text-decoration: none;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: #46466E;
}

.footer__box-email {
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: #24A3FF;
  text-decoration: none;
  margin-bottom: 30px;
  transition: color 0.3s;
}

.footer__box-email:hover {
  color: #F0145A;
}

.footer__box-copyright {
  font-size: 16px;
  line-height: 20px;
  color: #B3B3BA;
  text-align: center;
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .footer__box {
    display: grid;
    grid-template-columns: auto auto auto auto 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-row-gap: 21px;
  }
  .footer__box-link {
    grid-column-start: 4;
    margin: 0;
  }
  .footer__box-address {
    grid-column-start: 1;
    grid-column-end: 4;
    display: inline-flex;
    align-items: center;
    margin-right: 10px;
  }
  .footer__box-address::after {
    content: '';
    width: 6px;
    height: 6px;
    background-color: #B3B3BA;
    display: inline-flex;
    border-radius: 50%;
    margin-left: 6px;
  }
  .footer__box-copyright {
    grid-column-start: -1;
    grid-column-end: 5;
    grid-row-start: 1;
    text-align: right;
  }
  .footer__box-facebook {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 2;
    margin-right: 10px;
  }
  .footer__box-phone {
    grid-column-start: 2;
    grid-row-start: 2;
    margin: 0;
    display: inline-flex;
    align-items: center;
  }
  .footer__box-phone::before {
    content: '';
    width: 6px;
    height: 6px;
    background-color: #B3B3BA;
    display: inline-flex;
    border-radius: 50%;
    margin-right: 6px;
  }
  .footer__box-phone::after {
    content: '';
    width: 6px;
    height: 6px;
    background-color: #B3B3BA;
    display: inline-flex;
    border-radius: 50%;
    margin-left: 6px;
  }
  .footer__box-email {
    margin: 0;
    grid-column-start: 3;
    grid-column-end: span 3;
    grid-row-start: 2;
    margin-left: 10px;
  }
}
